@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;300;400;500;700&display=swap');

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body, 
input,
button {
    font-family: 'Josefin Sans', sans-serif;
}

body {
    max-height: 100vh;
    max-width: 100vh;
    overflow: hidden;
}